// Font
$font-family-base: "Poppins";
$font-family-second: "Prompt";
$enable-smooth-scroll: false;
$fontSize: 10px;

// Границы адаптива ======================
$minWidth: 320px; // Указываем минимальный размер экрана
$maxWidth: 1920; // Указываем размер полотна
$maxWidthContainer: 1600; // Указываем размер ограничивающего контейнера

$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

//<ОСНОВНАЯ СЕТКА>================================================================
._container {
	max-width: $maxWidthContainer + px;
	margin: 0 auto;
	@media (max-width: $md1+px) {
		max-width: 115.4rem;
		padding: 0 1rem;
	}
	@media (max-width: $md2+px) {
		max-width: 750px;
	}
	@media (max-width: $md3+px) {
		max-width: none;
	}
}
//</ОСНОВНАЯ СЕТКА>=================================================================
// Переменные
$border-radius: 1rem;
$br: $border-radius;
$gutter: 3rem;
/*
 * Color system
 * ---------------------------------------------
 */

$white: #fff;
$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #6c757d;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;
$black:    #000;

// fusv-disable
$grays: (
	"100": $gray-100,
	"200": $gray-200,
	"300": $gray-300,
	"400": $gray-400,
	"500": $gray-500,
	"600": $gray-600,
	"700": $gray-700,
	"800": $gray-800,
	"900": $gray-900
);

// scss-docs-end colors-map

$primary:       #F36D86;
$primary-light: #EF8FA1;
$secondary:     #F4B1BD;
$success:       green;
$info:          #666565;
$warning:       yellow;
$danger:        red;
$accent-light:  #293459;
$accent:        #1F2743;
$dark:          #252424;
$dark-second:   #1A2138;


/**
 * Buttons
 * --------------------------------------
 */
$btn-border-radius: .6rem;

$btn-font-size: 1.8rem;

