.header {
	flex: 0 0 100%;
	background: $primary 0 0 no-repeat;
	position: relative;

	@media (max-width: $md3+px) {
		overflow: hidden;
	}
	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		right: 0;
		width: 100%;
		height: 100%;
		background: url(../images/header/waveMain.webp) bottom right no-repeat;
		@media (max-width: $md3+px) {
			bottom: -20rem;
		}
	}
	&::before {
		content: "";
		position: absolute;
		bottom: 10rem;
		right: -10rem;
		width: 100%;
		height: 100%;
		background: url(../images/header/waveSecond.webp) bottom right no-repeat;
		@media (max-width: $md3+px) {
			right: -15rem;
		}
	}

	// .header__content
	&__content {
		position: relative;
		z-index: 3;
		padding-top: 9rem;
	}

	// .header__wrapperMenu
	&__wrapperMenu {
		position: fixed;
		width: 100%;
		left: 0;
		top: 0;
		z-index: 10;
		background-color: $primary;
		transition: all 0.5s ease 0s;
		min-width: 100%;
	}

	// .header__menu
	&__menu {
	}

	// .header__greetings
	&__greetings {
	}
	&._active {
		.header__wrapperMenu {
			background-color: #f36d8673;
		}
	}
}

.menu {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 2.3rem 1rem;
	@media (max-width: $md1+px) {
		padding: 2rem 1rem;
	}
	@media (max-width: $md3+px) {
		padding: 0.5rem 1rem 1rem;
	}

	// .menu__logo
	&__logo {
		color: white;
		width: 13.2rem;
		height: 3.3rem;
		position: relative;
		@media (min-width: $md1+px) {
			width: 25rem;
			height: 8rem;
		}
		@media (max-width: $md3+px) {
			width: 15rem;
			height: 4.5rem;
		}
		@media (max-width: $md4+px) {
			width: 13.2rem;
			height: 3.3rem;
		}
	}

	// .menu__links
	&__links {
		display: flex;
		position: relative;
		justify-content: space-between;
		min-width: 40%;
	}

	// .menu__link
	&__link {
		position: relative;
		font-family: $font-family-second;
		font-style: normal;
		font-weight: 500;
		font-size: 1.8rem;
		line-height: 27/18;
		opacity: 0.8;
		color: $white;
		transition: all 0.3s ease 0s;

		&._active {
			opacity: 1;
		}

		@media (min-width: $md2+px) {
			&:hover {
				opacity: 1;
			}
		}
		@media (min-width: $md1+px) {
			font-size: 2.4rem;
		}
		@media (min-width: 1920px) {
			font-size: 3rem;
		}

		&._active {
			opacity: 1;
		}

		@media (max-width: $md3+px) {
			color: $primary;
			font-size: 2.8rem;
			margin-bottom: 5rem;
			display: block;
			text-align: center;
		}
	}

	// .menu__animation-wrap
	&__animation-wrap {
		position: absolute;
		display: none;
		align-items: center;
		justify-content: space-between;
	}

	// .menu__round
	&__round {
		width: 0.6rem;
		height: 0.6rem;
		background-color: #fff;
		border-radius: 50%;
		&:first-child {
			transform: translateX(0.45rem);
		}
		&:last-child {
			transform: translateX(-0.45rem);
		}
	}

	// .menu__line
	&__line {
		height: 0.3rem;
		background-color: #fff;
	}

	// .menu__btn,btn
	&__btn {
		@media (max-width: $md3+px) {
			margin: 0 auto;
		}
	}
}

.icon {
}
.icon-logo {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	@media (max-width: $md4+px) {
		top: 20%;
	}
}
.greetings {
	@media (min-width: $md3+px) {
		display: flex;
	}

	// .greetings__model
	&__model {
		flex: 0 0 50%;
		position: relative;
		padding-bottom: 37%;
		@media (max-width: $md3+px) {
			flex: 0 0 40%;
			padding-bottom: 45%;
		}
	}

	// .greetings__model-image
	&__model-image {
		position: absolute;
		right: -12%;
		top: 11%;
		width: 100%;
		height: 100%;
		object-fit: contain;
		z-index: 2;
		transform: scale(1.25);
		@media (max-width: 1100px) {
			right: -10%;
			top: 4%;
		}
		@media (max-width: $md3+px) {
			right: -2%;
			top: 5%;
			transform: scale(1.35);
		}
		@media (max-width: $md4+px) {
			right: 0%;
			top: -3%;
			transform: scale(1.5);
		}
	}

	// .greetings__interactive
	&__interactive {
		flex: 1 1 auto;
		margin: 0 1.5rem;
		padding-top: 13rem;
		@media (max-width: 1300px) {
			padding-top: 7%;
		}
		@media (max-width: $md3+px) {
			margin: 0 1rem;
		}
	}
}
.interactive {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: left;

	@media (min-width: $md3+px) {
		max-width: 54rem;
	}

	// .interactive__top
	&__top {
		margin-bottom: 31.5%;
		z-index: 2;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		@media (max-width: 1500px) {
			margin-bottom: 20%;
		}
		@media (max-width: 1300px) {
			margin-bottom: 16%;
		}
		@media (max-width: $md3+px) {
			margin-bottom: 7%;
		}
		@media (max-width: $md4+px) {
			margin-bottom: 10%;
		}
	}

	// .interactive__text
	&__text {
		color: #fff;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
	}

	// .interactive__text-first
	&__text-first {
		margin-bottom: 1.3rem;
	}

	// .interactive__text-second
	&__text-second {
		margin-bottom: 1.3rem;
	}

	// .interactive__text-thrid
	&__text-thrid {
		margin-bottom: 3rem;
	}

	// .interactive__buttons
	&__buttons {
		display: flex;
		align-items: center;
		justify-content: flex-start;
	}

	// .interactive__visit
	&__visit {
		margin-right: 2.5rem;
	}

	// .interactive__play
	&__play {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		position: relative;
		@media (min-width: $md2+px) {
			&:hover {
				.interactive__play-button {
					animation-name: ping-pong;
					animation-duration: 1s;
					animation-timing-function: ease-in;
					animation-iteration-count: infinite;
					animation-direction: alternate;
				}

				.interactive__play-text:before {
					animation-name: ping-pong-shadow;
					animation-duration: 1s;
					animation-timing-function: ease-in;
					animation-iteration-count: infinite;
					animation-direction: alternate;
				}
			}
		}
	}
	@keyframes ping-pong {
		0% {
			transform: translateY(0);
		}
		50% {
			transform: translateY(-1rem);
		}
		100% {
			transform: translateY(1rem);
		}
	}
	@keyframes ping-pong-shadow {
		0% {
			box-shadow: 0.4rem 0.9rem 0.4rem $accent;
		}
		50% {
			box-shadow: 0.45rem 1rem 0.5rem $accent;
		}
		100% {
			box-shadow: 0.4rem 0.9rem 0.15rem $accent;
		}
	}

	// .interactive__play-button
	&__play-button {
		border-radius: 50%;
		width: 3rem;
		height: 3rem;
		margin-right: 1rem;
		background-color: $accent;
		position: relative;
		transition: all 0.3s ease 0s;

		&::before {
			// треугольник
			content: "";
			position: absolute;
			top: 1rem;
			left: 0.6rem;
			width: 1.1rem;
			height: 1.1rem;
			background: $white;
			border-radius: 0.5px;
			transform: rotate(36deg) skew(-15deg);
		}
		&::after {
			// закрывающий квадрат
			content: "";
			position: absolute;
			width: 1rem;
			height: 1.7rem;
			background: $accent;
			top: 0.65rem;
			left: 0.25rem;
		}
	}

	// .interactive__play-text
	&__play-text {
		@include responsive-tlt(16px, 22px);
		font-family: $font-family-second;
		font-weight: 500;
		color: $accent;
		transition: all 0.3s ease 0s;
		&::before {
			content: "";
			position: absolute;
			color: transparent;
			width: 2rem;
			height: 0.15rem;
			left: 0rem;
			bottom: 0;
			border-radius: 50%;
			box-shadow: 0.4rem 0.9rem 0.4rem $accent;
		}
	}

	// .interactive__bot
	&__bot {
		display: flex;
		align-items: center;
		color: $white;
		margin-bottom: 3rem;
		position: relative;
		z-index: 2;
	}

	// .interactive__line
	&__line {
		margin-right: 1rem;
		flex: 1 1 auto;
		height: 0.1rem;
		background-color: $white;
	}

	// .interactive__textFollow
	&__textFollow {
		margin-right: 1.5rem;
		font-family: $font-family-second;
		font-weight: 500;
	}

	// .interactive__socialLinks
	&__socialLinks {
		display: flex;
		align-items: center;
	}

	// .interactive__socialLink
	&__socialLink {
		margin-right: 1.5rem;
		width: 2.4rem;
		height: 2.4rem;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		display: block;
		border: 0.2rem solid $white;
		border-radius: 50%;
		transition: all 0.3s ease 0s;
		&:last-child {
			margin: 0px 0px 0px 0px;
		}
		&:hover {
			background-color: $accent;
		}
		svg {
			position: absolute;
			width: 1rem;
			height: 1rem;
			top: 0.5rem;
			left: 0.5rem;
			color: $white;
		}
	}
}
