.testimonial {
	// .testimonial__content
	&__content {
	}

	// .testimonial__row
	&__row {
		@media (min-width: $md4+px) {
			display: flex;
			justify-content: flex-start;
			align-items: center;
		}
	}

	// .testimonial__image
	&__image {		
			flex: 0 1 50%;
			padding-bottom: 45%;
			position: relative;
			margin-right: 7rem;		
		@media (max-width: $md3+px) {
			margin-right: 4rem;
			flex: 0 1 30%;
		}
		@media (max-width: $md4+px){
			display:none;
		}

		img {
			object-position: center;
			object-fit: contain;
			transform: scale(1.2);
		}
	}

	// .testimonial__column
	&__column {
		flex: 0 1 50%;
		overflow: hidden;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		@media (max-width: $md3+px) {
			flex: 0 1 70%;
		}
	}

	// .testimonial__title
	&__title {
		margin-bottom: 2.5rem;
		@media (max-width: $md2+px) {
			margin-bottom: 1.5rem;
		}
	}

	// .testimonial__slider
	&__slider {
		margin-bottom: 3rem;
		max-width: 100%;
		@media (max-width: $md2+px) {
			margin-bottom: 1.5rem;
		}
	}

	// .testimonial__symbols
	&__symbols {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
	}

	// .testimonial__buttons
	&__buttons {
		display: flex;

		&_right,
		&_left {
			width: 4rem;
			height: 4rem;
			background-color: $accent;
			box-shadow: 0px 5px 20px rgba(31, 39, 67, 0.2);
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			&:first-child {
				margin-right: 3rem;
			}
		}

		&_left {
			.icon-arrow-testy {
				transform: rotate(180deg);
			}
		}
	}

	// .testimonial__brakets
	&__brakets {
		display: flex;
		&_big {
			margin-right: 2rem;
			.icon-brakets {
				width: 6.8rem;
				height: 6.8rem;
				color: $primary;
				transform: translate(-2rem, -2rem);
			}
			@media (max-width: $md2+px) {
				.icon-brakets {
					width: 4.5rem;
					height: 4.5rem;
				}
				margin-right: 1rem;
			}
		}
		&_small {
			.icon-brakets {
				width: 3.8rem;
				height: 3.8rem;
				color: $primary;
				transform: rotate(180deg) translate(2rem, -1.5rem);
				@media (max-width: $md2+px) {
					width: 2.4rem;
					height: 2.4rem;
				}
			}
		}
	}
}

.slider {
	// .slider__slide-testimonial
	&__slide-testimonial {
	}

	// .slider__text
	&__text {
		margin-bottom: 2rem;
		@media (max-width: $md2+px) {
			margin-bottom: 1rem;
		}
	}

	// .slider__name
	&__name {
		margin-bottom: 1rem;
		display: inline-block;
		position: relative;
	}

	// .slider__line
	&__line{
		display: inline-block;
		width: 10rem;
		height: 0.1rem;
		left: 14rem;
		bottom: .5rem;
		background-color: $black;
		margin-left: 1rem;
		@media (max-width: $md2+px){
			width: 5rem;
			margin-left: .5rem;
		}
	}

	// .slider__profession
	&__profession {
		display: inline-block;
	}
}
.icon {
}
.testimonial__buttons .icon-arrow-testy {
	width: 1.6rem;
	height: 1.1rem;
	color: $white;
}
.testimonial__buttons .icon-brakets {
	color: $primary;
}
