//<ОСНОВНОЙ БЛОК>====================================================================================================
body {
	font-size: 1.4rem;
	font-family: $font-family-base;
	color: $black;	
	&._lock {
		overflow: hidden;
	}
}

.page {
	//  ? Когда нужноприменить общие стили ко странице в многостраничном сайте
	flex: 1 1 auto;
}

// new ================================================
.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
}

