.beautyModels {

	// .beautyModels__content
	&__content {
		position: relative;
	}

	// .beautyModels__row
	&__row {
		display: flex;
		justify-content: flex-start;
		min-width: 0;
		@media (max-width: $md3+px) {
			flex-direction: column;
		}
	}

	// .beautyModels__column
	&__column {
		flex: 0 0 50%;
		min-width: 0;

		@media (max-width: $md3+px) {
			flex: 1 1 auto;
		}
	}

	// .beautyModels__mainFoto
	&__mainFoto {
		margin-right: 12.5rem;
		position: relative;
		z-index: 1;
		max-width: 45.5rem;
		@media (max-width: 1400px) {
			margin-right: 5rem;
		}
		@media (max-width: $md2+px) {
			margin-right: 2.5rem;
		}
		@media (max-width: $md3+px) {
			margin-right: 0;
			max-width: 100%;
		}
	}

	// .beautyModels__trends
	&__trends {
		display: flex;
		flex-direction: column;
	}
}
.icon {
}
.icon-cubik {
	position: absolute;
	left: -6.5rem;
	top: -5rem;
	color: $accent;
	opacity: 0.4;
	width: 25rem;
	height: 32rem;
	@media (max-width: $md2+px){
		top: -3rem;
	}
	@media (max-width: $md3+px) {
		top: 20%;
		left: 55%;
	}
	@media (max-width: $md4+px) {
		transform: scale(0.65);
	}
}

.mainFoto {
	position: relative;

	// .mainFoto__slider
	&__slider {
		min-width: 0;
	}

	// .mainFoto__slide
	&__slide {
		max-width: 100%;
	}

	// .mainFoto__image
	&__image {
		flex: 1 0 auto;
		position: relative;
		padding: 0 70% 126% 0;
		@media (max-width: 1300px) {
			padding: 0 100% 124.5% 0;
		}
		@media (max-width: $md3+px) {
			padding: 0 60% 75% 0;
			order: 2;
		}
		@media (max-width: 620px) {
			padding: 0 60% 85% 0;
		}
		@media (max-width: $md4+px) {
			padding: 0 50% 95% 0;
		}
		img {
			object-fit: contain;
		}
	}

	// .mainFoto__text-block
	&__text-block {
		@media (max-width: 1300px) {
			display: flex;
			flex-direction: column;
			margin-bottom: 10rem;
			order: 2;
		}
		@media (max-width: $md3+px) {
			margin-bottom: 1rem;
			display: block;
		}
	}

	// .mainFoto__number
	&__number {
		flex: 0 1 auto;
		font-weight: 600;
		margin-bottom: 1.5rem;
		@media (max-width: $md3+px) {
			order: 3;
			margin-bottom: 0.5rem;
			display: inline-block;
			margin-right: 2rem;
		}
	}

	// .mainFoto__text
	&__text {
		flex: 0 1 auto;
		@media (min-width: 1300px) {
			max-width: 45rem;
		}
		@media (max-width: $md3+px) {
			flex: 1 1 auto;
			order: 4;
			display: inline;
		}
	}
}
.trends {
	// .trends__title
	&__title {
		margin-bottom: 3rem;
		@media (max-width: $md3+px) {
			margin-bottom: 1rem;
		}
	}

	// .trends__text-row
	&__text-row {
		margin-bottom: 3rem;
		&:last-child {
			margin: 0rem;
		}

		@media (min-width: 1300px) {
			display: flex;
			justify-content: flex-start;
			margin-bottom: 3rem;
		}
		@media (max-width: $md3+px) {
			margin-bottom: 1.5rem;
		}
	}

	// .trends__text-column
	&__text-column {
		flex: 0 1 50%;
		@media (min-width: 1300px) {
			max-width: 26rem;
			margin-right: 3rem;
			&:last-child {
				margin-right: 0rem;
			}
		}
		@media (max-width: 1300px) {
			margin-bottom: 1rem;
		}
	}

	// .trends__link
	&__link {
		margin-bottom: 4rem;
		display: block;
		color: inherit;
		position: relative;
		@media (max-width: $md2+px) {
			margin-bottom: 3rem;
			flex: 1 1 auto;
		}
		@media (max-width: $md4+px) {
			margin-bottom: 1.5rem;
		}

		&::after {
			content: "";
			position: absolute;
			width: 18rem;
			height: 0.15rem;
			background-color: $dark;
			top: 3rem;
			left: 0;
			@media (max-width: $md2+px) {
				width: 15rem;
			}
			@media (max-width: $md3+px) {
				width: 18rem;
				top: 3rem;
			}
			@media (max-width: $md4+px) {
				top: 2.5rem;
				width: 45%;
			}
		}
	}

	// .trends__text-block
	&__text-block {
		@media (min-width: 1300px) {
			display: none;
		}
	}
	// .trends__example
	&__example {
	}
}

.example-trends {
	max-width: 100%;
	@media (min-width: 1300px) {
		position: relative;
		max-width: 55rem;
	}

	// .example-trends__block
	&__block {
		position: relative;
	}

	// .example-trends__model
	&__model {
		position: relative;
		max-width: 17.5rem;
		@media (max-width: 1300px) {
			margin-right: 3rem;
		}
		@media (max-width: $md2+px) {
			margin-right: 1rem;
		}
		@media (max-width: $md3+px) {
			display: block;
		}
		&:last-child {
			margin-right: 0rem;
		}
		&._active {
			display: block;
		}
	}

	// .example-trends__foto
	&__foto {
		position: relative;
		padding-bottom: 125%;
		min-width: 16rem;
		overflow: hidden;
		img {
		}

		&::before {
			content: "";
			position: absolute;
			width: 6.7rem;
			height: 6.7rem;
			bottom: 0;
			right: 0rem;
			opacity: 0.65;
			border-radius: 8.2rem 0.5rem 0.5rem 0.5rem;
			background-color: $dark;
			cursor: pointer;
			z-index: 5;
			@media (max-width: $md4+px) {
				width: 3.7rem;
				height: 3.7rem;
			}
		}
		&::after {
			content: "";
			position: absolute;
			bottom: 1.3rem;
			right: 1rem;
			-webkit-mask-image: url(../images/beautyModelsSlider/zoomLens.webp);
			-webkit-mask-size: 100%;
			-webkit-mask-repeat: no-repeat;
			-webkit-mask-position: center;
			background-color: $white;
			z-index: 6;
			width: 2.4rem;
			height: 2.4rem;
			cursor: pointer;
			@media (max-width: $md4+px) {
				width: 2rem;
				height: 2rem;
				bottom: 0.5rem;
				right: 0.5rem;
			}
		}
		@media (max-width: $md4+px) {
			min-width: 9rem;
			max-width: 12rem;
			padding-bottom: 100%;
		}
	}

	// .example-trends__number
	&__number {
		@media (max-width: $md3+px) {
			display: none;
		}
	}

	// .example-trends__text
	&__text {
		text-overflow: ellipsis;
		overflow: hidden;
		max-height: 6rem;

		@media (max-width: $md2+px) {
			max-width: 18rem;
		}
		@media (max-width: $md3+px) {
			display: none;
		}
	}
}
.image-mb {
	margin-bottom: 2rem;
	margin-right: 1rem;
	@media (max-width: $md3+px) {
		margin-bottom: 1.5rem;
	}
}

.icon-arrow {
	padding: 0.5rem;
	position: absolute;
	z-index: 5;
	width: 2.7rem;
	height: 3.8rem;
	left: 60rem;
	bottom: 25rem;
	cursor: pointer;
	@media (max-width: 1300px) {
		left: 65%;
		bottom: 37.5rem;
	}
	@media (max-width: $md3+px) {
		display: none;
	}
}

.example-trends__arrows-prev > .icon-arrow {
	padding: 0.5rem;
	position: absolute;
	z-index: 5;
	width: 2.7rem;
	height: 3.8rem;
	bottom: 18rem;
	left: 60rem;
	cursor: pointer;
	transform: rotate(180deg);

	@media (max-width: 1500px) {
		// right: -1rem;
	}
	@media (max-width: 1300px) {
		left: 55%;
		bottom: 37.5rem;
	}
	@media (max-width: $md3+px) {
		display: none;
	}
}
