.about {
	// .about__content
	&__content {
	}

	// .about__row
	&__row {
		@media (min-width: $md4+px) {
			display: flex;
			justify-content: flex-start;
			align-items: center;
		}
	}

	// .about__text-block
	&__text-block {
		flex: 0 1 50%;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		padding: 0 1rem 0 0;
		@media (max-width: $md2+px) {
			flex: 0 1 70%;
		}
	}

	// .about__visual-block
	&__visual-block {
		flex: 0 1 50%;
		width: 100%;
		position: relative;
		padding-bottom: 45%;
		@media (max-width: $md2+px) {
			flex: 0 1 30%;
		}
		@media (max-width: $md4+px) {
			display: none;
		}
	}
}

.text-block {
	// .text-block__title
	&__title {
		margin-bottom: 2rem;
		@media (max-width: $md3+px) {
			margin-bottom: 1rem;
		}
	}

	// .text-block__text
	&__text {
		*:last-child{
		margin: 0px 0px 0px 0px;
		}
		p {
			margin-bottom: 1.5rem;
			@media (max-width: $md1+px) {
				margin-bottom: 1rem;
			}
		}
		margin-bottom: 3.5rem;
		@media (max-width: $md1+px) {
			margin-bottom: 2rem;
		}
		@media (max-width: $md3+px) {
			margin-bottom: 1rem;
		}
	}

	// .text-block__button
	&__button {
	}
}

.visual-block {
	// .visual-block__image
	&__image {
		object-fit: contain;
		transform: scale(1.25) translate(15%, -3%);
		@media (max-width: $md1+px) {
			transform: scale(1.2) translate(0%, -4.5%);
		}
		@media (max-width: $md2+px) {
			transform: scale(1.5) translate(5%, 15%);
		}
		@media (max-width: $md3+px){
			transform: scale(1.2) translate(0%, 15%);
		}
	}
}
