._tabs-block {
	display: none;
	&._active {
		display: block;
	}
}

.tabs-block {
	// .tabs-block__nav
	&__nav {
		margin: 0 auto;
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		margin-bottom: 4.5rem;
		@media (max-width: $md3+px){
			margin-bottom: 2.5rem;
		}
		@media (max-width: $md4+px){
			display: block;
			text-align: center;
			margin-bottom: 1.5rem;
		}
	}

	// .tabs-block__item
	&__item {
		font-family: $font-family-base;
		font-style: normal;
		font-weight: normal;
		line-height: 1.6;
		font-size: 1.6rem;
		border: 1px solid rgba(16, 26, 48, 0.1);
		color: $dark-second;
		padding: 1rem 2rem;
		&._active {
			border-radius: 0.5rem;
			background-color: $primary;
			color: $white;
		}
		@media (max-width: 630px){
			padding: .5rem 1rem;
			font-size: 1.4rem;
		}	
		@media (max-width: $md4+px){
			font-size: 1.6rem;
		}	
	}

	// .tabs-block__body
	&__body {
	}

	// .tabs-block__block
	&__block {
	}

	// .tabs-block__grid
	&__grid {
	}
}

