.grid {
	// .grid__body
	&__body {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
		margin: auto;
		gap: $gutter;
		@media (max-width: 510px){
			grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
			gap: 1.5rem;
		}
		@media (max-width: 410px){
			grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
			gap: .5rem;
		}
	}

	// .grid__item
	&__item {
		overflow: hidden;		
		img {
			border-radius: $br;
		}
		&::after {
			content: "Shop Now";
			font-family: $font-family-base;
			font-style: normal;
			font-weight: 500;
			font-size: 2rem;
			line-height: 1.5;
			text-align: center;
			color: $white;
			position: absolute;
			width: 100%;
			height: 7.2rem;
			bottom: 0;
			left: 0;
			background-color: #1F2743;
			border-radius: 0px 0px $br $br;
			opacity: .9;
			display: flex;
			justify-content: center;
			align-items: center;
			transform: translateY(105%);
			transition: all 0.3s ease 0s;
		}
		@media (min-width: $md2+px) {
		&:hover {
			&::after {
			transform: translateY(0%);
			}
		}
		}
		

		// .grid__item_big
		&_big {
			grid-row: span 11;
			@media (max-width: 510px){
				grid-row: span 18;
			}
			@media (max-width: 410px){
				grid-row: span 36;
			}
		}

		// .grid__item_small
		&_small {
			grid-row: span 8;
			@media (max-width: 510px){
				grid-row: span 12;
			}
			@media (max-width: 410px){
				grid-row: span 29;
			}
		}
	}
}
