.icon-menu {
	display: none;
	@media (max-width: $md3+px) {
		display: block;
		position: absolute;
		top: 1.8rem;
		right: 1.5rem;
		width: 3rem;
		height: 1.8rem;
		cursor: pointer;
		z-index: 5;
		span {
			transition: all 0.3s ease 0s;
			top: calc(50% - 1px);
			left: 0;
			position: absolute;
			width: 100%;
			height: 0.2rem;
			background-color: $white;
			&:first-child {
				top: 0;
			}
			&:last-child {
				top: auto;
				bottom: 0;
			}
		}
		&._active {
			span {
				background-color: $accent;
				transform: scale(0);
				&:first-child {
					transform: rotate(-45deg);
					top: calc(50% - 1px);
				}
				&:last-child {
					transform: rotate(45deg);
					bottom: calc(50% - 1px);
				}
			}
		}
	}
}

// .menu__body
.menu__body {
	display: none;
	@media (max-width: $md3+px) {
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		transform: translate(0px, -100%);
		transition: 0.8s ease;
		position: fixed;
		z-index: 3;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		// ? Позволяет списку выпадающего меню прокручиваться/ ставиться по необходимости
		overflow: auto;
		padding: 30px 30px 30px 30px;
		background-color: rgb(255, 255, 255);
		&._active {
			transform: translate(0px, 0%);
		}
	}
}

// menu__list
.menu__list{
	padding-top: 10rem;
}