.subscribe {
	// .subscribe__content
	&__content {
	}

	// .subscribe__wrap
	&__wrap {
	}

	// .subscribe__top-block
	&__top-block {
		height: 4rem;
		margin: 0 4rem;
		border-radius: 4rem 4rem 0 0;
		background-color: $primary;
		opacity: 0.5;
		@media (max-width: $md3+px){
			height: 2.5rem;
			margin: 0 2.5rem;
			transform: translateY(.5rem);
		}
	}

	// .subscribe__bottom-block
	&__bottom-block {
		border-radius: 4rem;
		background-color: $primary;
		padding: 9.5% 0 9.5% 0;
		@media (max-width: $md4+px){
			padding: 7% 0 7% 0;
		}
	}

	// .subscribe__formNews
	&__formNews {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
	}
}

.formNews {
	// .formNews__title
	&__title {
		margin-bottom: 1.5rem;
		color: $white;
		text-align: center;
		padding: 0 2rem;
	}

	// .formNews__text
	&__text {
		margin-bottom: 4.5rem;
		color: $white;
		text-align: center;
		padding: 0 3rem;
	}

	// .formNews__email
	&__email {
		margin-bottom: 5rem;
		background-color: $white;
		padding: 1.7rem 2.3rem;
		font-weight: 400;
		font-style: normal;
		font-size: 1.6rem;
		line-height: 1;
		color: #b4b2b2;
		border-radius: 1rem;
		width: 62%;
		&:focus {
			box-shadow: 0 0 3rem $accent-light;
			color: $accent;
		}
	}

	@media (max-width: $md4+px){
		&__text{
			margin-bottom: 2rem;
			padding: 0 2rem;
		}
		&__email{
			margin-bottom: 2.5rem;
			padding: 1rem 1.5rem;
			width: 80%;
		}
	}
}
