.footer {
	background-color: $dark-second;

	// .footer__content
	&__content {
		width: 100%;
		padding: 9rem 1rem;
		@media (max-width: $md2+px) {
			padding: 6rem 1rem;
		}
		@media (max-width: $md3+px) {
			padding: 3rem 1rem;
		}
	}

	// .footer__row
	&__row {
		@media (max-width: $md3+px){
			flex-wrap: wrap;
		}
		@media (min-width: $md3+px) {
			justify-content: space-between;
		}
		@media (min-width: $md4+px) {
			display: flex;
			align-items: flex-start;
			justify-content: flex-start;			
		}
	}

	// .footer__column
	&__column {
		
		@media (max-width: $md3+px) {
			flex: 0 1 46%;
			margin: 0 0 3rem 0;
			&:nth-child(odd){
				margin: 0 3rem 3rem 0;
			}			
		}
		@media (max-width: 500px){
			flex: 0 1 100%;
			margin: 0 0 2rem 0;
		}
		@media (min-width: $md3+px) {
			flex: 0 1 22%;
			color: $white;
			margin: 0 2.5rem 0 0;
			&:last-child {
				margin: 0px 0px 0px 0px;
			}
			&:nth-child(odd){
				flex: 0 1 28%;
			}
		}
		&:last-child {
			margin: 0px 0px 0px 0px;
		}		
	}
}

.ukFashion {
	// .ukFashion__title
	&__title {
		width: 13rem;
		height: 3.2rem;
		position: relative;

		svg {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
		}
	}

	// .ukFashion__text
	&__text {
	}

	// .ukFashion__social
	&__social {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;

		li {
			margin-right: 2rem;
			width: 5rem;
			height: 5rem;
			border-radius: 50%;
			background-color: $accent-light;
			display: flex;
			align-items: center;
			justify-content: center;
			transition: all 0.3s ease 0s;
			@media (min-width: $md2+px) {
				&:hover {
					background-color: $primary;
				}
			}
		}
	}

	// .ukFashion__social-link
	&__social-link {
		position: relative;
		width: 2rem;
		height: 2rem;
		display: block;

		svg {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			color: $white;
		}
	}
}
