.link-column {
	// .link-column__title
	&__title {
		margin-bottom: 2rem;
		color: $white;
	}
	// .link-column__text
	&__text {
		color: $white;
		margin-bottom: 1.5rem;
		display: block;
		@media (max-width: $md3+px) {
			margin-bottom: 2rem;
		}
		&:last-child {
			margin: 0px 0px 0px 0px;
		}
	}
}
a.link-column__text:hover {
	@media (min-width:$md2+px){
		color: $primary;
	}	
}
