@use "sass:math";

.icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    fill: currentColor;
}

.icon-arrow {
    width: math.div(17, 28) * 1em;
}
.icon-arrow-testy {
    width: math.div(17, 28) * 1em;
}
.icon-arrow-up {
    width: math.div(32, 32) * 1em;
}
.icon-brakets {
    width: math.div(68, 68) * 1em;
}
.icon-cubik {
    width: math.div(248, 317) * 1em;
}
.icon-facebookAlone {
    width: math.div(10, 20) * 1em;
}
.icon-logo {
    width: math.div(131, 32) * 1em;
}
.icon-playIcon {
    width: math.div(9, 10) * 1em;
}
.icon-tweetAlone {
    width: math.div(21, 17) * 1em;
}
.icon-underline {
    width: math.div(57, 6) * 1em;
}
.icon-wAlone {
    width: math.div(20, 17) * 1em;
}
.icon-whatsAppAlone {
    width: math.div(10, 10) * 1em;
}
