/**
 * Responsive titles
 */
h1, .h1
    // @include responsive-tlt(50px, 60px)
    line-height: 130%
    font-family: $font-family-second
    font-weight: 600
    letter-spacing: 3px
    font-style: normal
    font-size: 6rem

    @media (max-width: $md1+px)
            font-size: 6rem
    @media (max-width: 1400px)
            font-size: 5rem
    @media (max-width: $md2+px)
            font-size: 4rem
    @media (max-width: $md3+px)
            font-size: 3rem
    @media (max-width: $md4+px)
            font-size: 2.6rem

h2, .h2
    // @include responsive-tlt(34px, 41px)
    line-height: 130%
    font-family: $font-family-second
    font-weight: 600
    font-style: normal
    font-size: 4.1rem

    @media (max-width: $md1+px)
            font-size: 4.1rem
    @media (max-width: $md2+px)
            font-size: 3.4rem
    @media (max-width: $md3+px)
            font-size: 2.8rem
    @media (max-width: $md4+px)
            font-size: 2.2rem

h3, .h3
    // @include responsive-tlt(19px, 23px)
    font-family: $font-family-second
    font-style: normal
    font-weight: 500
    line-height: 1.3
    font-size: 2.3rem
    
    @media (max-width: $md1+px)
            font-size: 2.3rem
    @media (max-width: $md2+px)
            font-size: 1.9rem
    @media (max-width: $md3+px)
            font-size: 2.3rem
    @media (max-width: $md4+px)
            font-size: 1.8rem

h4, .h4
    // @include responsive-tlt(1.6rem, 2rem)
    font-family: $font-family-base
    font-style: normal
    font-weight: normal
    line-height: 1.5
    font-size: 2rem

    @media (max-width: $md1+px)
            font-size: 2rem
    @media (max-width: $md2+px)
            font-size: 1.8rem
    @media (max-width: $md3+px)
            font-size: 1.6rem
    @media (max-width: $md4+px)
            font-size: 1.4rem

h5, .h5
    // @include responsive-tlt(1.6rem, 1.8rem)
    font-family: $font-family-base
    font-style: normal
    font-weight: normal
    line-height: 1.5
    font-size: 1.8rem

    @media (max-width: $md1+px)
            font-size: 1.8rem
    @media (max-width: $md2+px)
            font-size: 1.6rem
    @media (max-width: $md3+px)
            font-size: 1.4rem
    @media (max-width: $md4+px)
            font-size: 1.2rem

h6, .h6
    // @include responsive-tlt(15px, 20px)
    font-family: $font-family-base
    font-style: normal
    font-weight: 600
    line-height: 1.6
    font-size: 2rem

    @media (max-width: $md1+px)
            font-size: 2rem
    @media (max-width: $md2+px)
            font-size: 1.6rem
    @media (max-width: $md3+px)
            font-size: 1.4rem
    @media (max-width: $md4+px)
            font-size: 1.2rem
		

